
@media (orientation: landscape) {

  .App {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    background-image: url("./assets/images/fondo-hoja-cuadriculada.jpg");
    background-repeat: repeat;
    background-position: center center;
    background-attachment: scroll;
    height: 100%;
    width: 100%;
    min-height: 100vh;
    min-width: 100vw;
  }

  .Container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    width: 85vw;
    min-height: 100vh;
    background-image: url("./assets/images/hoja-blanca-texturizada.jpg");
    background-repeat: repeat;
  }

  .Text {
    font-family: "Pastel Crayon";
  }
}

